<template>
    <div>
        <div class="talk-bar">
      <ul>
        <li v-for="(item,index) in talkList" :key="index">
          <div class="right">
            <span>{{item.question}}</span>
          </div>
          <div class="left">
            <span>{{item.answer}}</span>
          </div>
        </li>
      </ul>
    </div>
    <div class="talk-form">
      <input type="text" v-model="msg">
      <button v-on:click="send">发送</button>
    </div>
    </div>
</template>
<script>
import md5 from 'js-md5';
export default {
  created(){
    let obj = {
      title:"聊天",
      className:"talk"
    }
    this.$emit("changeNav",obj)
  },
  data(){
    return{
      msg:'',
      talkList:[]
    }
  },
  methods: {
    getTimeStamp(){
      let dataStr=Date.parse(new Date());
      dataStr=dataStr.toString().substr(0,10);//第十位不包括第十位
      return dataStr;
    },
    getNonceStr(){
      let str =Math.random().toString(36).substr(2);
      return str;
    },
    send(){
      let proxy='https://bird.ioliu.cn/v2?url=';
      let url='https://api.ai.qq.com/fcgi-bin/nlp/nlp_textchat';
      let params={
          app_id:"2111982969",
          time_stamp:this.getTimeStamp(),
          nonce_str:this.getNonceStr(),
          session:'10000',
          question:this.msg,
          sign:"",
        };
      let appKey="OglcGVdPRSapVYC1";
      params.sign=this.getReqSign(params,appKey);
      axios({
        url:proxy+url,
        method: 'post',
        data: {...params},
      }).then((res)=>{
        console.log(res)
        let obj = {
          question:this.msg,
          answer:res.data.data.answer
        }
        this.talkList.push(obj)
      })
    },
    getReqSign(params,appKey){
      let arr=[];
      let obj={};
      let str="";
      //1.对象排序
      arr=Object.keys(params).sort();
      arr.forEach((item,index,arr)=>{
        obj[item]=params[item]
      })
      //2.参数对按URL
      for(let item in obj){
        if(obj[item]!=''){
          str+=item+"="+encodeURI(obj[item])+"&"
        }
      }
      //3.将应用密钥以app_key为键名，组成URL键值拼接到字符串末尾
      str+='app_key='+appKey;
      //4.对字符串进行MD5加密
       str = md5(str).toUpperCase();
      return str
    }
  },
}
</script>
<style scoped>
.talk-form{
    width: 100%;
    padding-top:.3rem;
    height: 1rem;
    background-color: #ddd;
    text-align: center;
    position: fixed;
    bottom: 1rem;
  }
  .talk-bar{
    padding: 20px
  }
  .talk-bar span{
    padding:5px 10px;
    background-color: rgb(195, 240, 195);
    border-radius: 10px;
  }
  .talk-bar .left{
    text-align: left;
    padding:10px;
  }
  .talk-bar .right{
    text-align: right;
    padding:10px;
  }
</style>
